<template>
  <div class="container">
    <Dropdown class="is-pulled-right" @on-click="clickHandler">
      <a href="javascript:void(0)">
        {{ nickname || "rotab" }}
        <Icon type="ios-arrow-down"></Icon>
      </a>
      <DropdownMenu slot="list">
        <DropdownItem name="update-password">修改密码</DropdownItem>
        <DropdownItem name="logout">注销</DropdownItem>
      </DropdownMenu>
    </Dropdown>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  computed: {
    ...mapState("user", {
      nickname: state => state.nickname
    })
  },
  methods: {
    clickHandler(name) {
      switch (name) {
        case "logout":
          localStorage.clear();
          this.$router.push("/");
          break;
        case "update-password":
          this.$router.push("/user/password");
          break;
        default:
          break;
      }
    }
  }
};
</script>

<style></style>
