<template>
  <Menu width="auto" class="is-fullheight">
    <div v-for="(menu, i) in menus" :key="i">
      <Submenu
        v-if="menu.children && menu.children.length > 0"
        :name="'submenu-' + menu.name"
      >
        <template slot="title">
          {{ menu.name }}
        </template>
        <MenuItem
          v-for="(child, j) in menu.children"
          :name="child.name"
          :key="child.path + j"
          :to="child.path"
        >
          {{ child.name }}
        </MenuItem>
      </Submenu>
      <MenuItem v-else :name="'menuItem-' + menu.name" :to="menu.path">
        {{ menu.name }}
      </MenuItem>
    </div>
  </Menu>
</template>
<script>
import routes from "../../router/routes";
export default {
  data() {
    return {
      menus: routes[1].children
    };
  }
};
</script>
